.pagination-link {
  padding: 0.5rem 1rem;
  border: none;
  background: none;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.pagination-link:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.pagination-link.active {
  color: #7c3aed; /* Tailwind's blue-600 */
  font-weight: bold;
}
